.alert{
	padding: 0.75rem 1.25rem;
	color: var(--mainWhite);
	text-align: center;
	margin: 2rem auto 0 auto;
	border-radius: 2px;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px;
	-ms-border-radius: 2px;
	-o-border-radius: 2px;
}
.alert-danger{
	background: var(--mainRed)
}
.alert-success{
	background: var(--mainGreen)
}